.card {
  position: relative;
  overflow: hidden;
}
.card:hover .card__content, .card:focus .card__content {
  left: 0;
}
.card:hover .card__figure-title {
  display: none;
}

.card__content {
  position: absolute;
  top: 0;
  left: -100%;
  z-index: 10;
  width: 100%;
  height: 100%;
  padding: 58px;
  display: flex;
  flex-direction: column;
  background-color: rgba(16, 38, 84, 0.89);
  color: #ffffff;
  transition: 0.3s ease;
  box-shadow: 0 4px 35px rgba(1, 28, 64, 0.15);
}
@media (max-width: 1023px) {
  .card__content {
    padding: 29px 35px 35px;
  }
}
@media (max-width: 767px) {
  .card__content {
    padding-left: 25px;
    padding-right: 25px;
  }
}

.card__desc {
  overflow: auto;
  margin-bottom: 10px;
}
.card__desc::-webkit-scrollbar {
  width: 0;
}

.card__content h3 {
  margin-top: 0;
  margin-bottom: 16px;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;
}
@media (max-width: 767px) {
  .card__content h3 {
    margin-bottom: 14px;
  }
}

.card__content ul {
  margin-top: 0;
  margin-bottom: 0;
}

.card__figure {
  margin: 0;
  position: relative;
  overflow: hidden;
  max-width: 100%;
  aspect-ratio: 260/294;
  transition: 0.3s ease;
}
@media (max-width: 1023px) {
  .card__figure {
    aspect-ratio: 268/294;
  }
}
@media (max-width: 767px) {
  .card__figure {
    aspect-ratio: 226/274;
  }
}
.card__figure::before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  content: "";
  background-image: green;
}

.card__figure img {
  max-width: none;
  width: auto;
  height: 100%;
}
.card__figure-title {
  position: absolute;
  bottom: 23px;
  left: 11.5384615385%;
  right: 11.5384615385%;
  z-index: 5;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;
  color: #ffffff;
}
